








import ut from '../../utils/index.js';
export default {
	data() {
		return {
			list:[]
		}
	},
	onShow() {
		this.req_problem();
	},
	methods: {
		req_problem(){
			ut.request({
				url: "aboutus/list"
			}).then(data=>{
				this.list=data;
			})
		}
	}
}



